.aboutblackbg {
  background-color: #1d1d1d;
  margin: auto;
  height: 420px;
  padding-top: 88px;
  @media (max-width: 768px) {
    width: 100%;
    height: fit-content;
    padding: 60px 20px 70px;
    word-break: break-word;
  }
  a,
  a:hover,
  :active,
  a:focus {
    color: white;
  }
}

.aboutWrap {
  display: flex;
  align-items: center;
  > a:last-child {
    margin-left: 20px;
  }

  @media (max-width: 768px) {
    justify-items: center;
    flex-direction: column;
    > a:last-child {
      margin-left: 0;
      > div {
        margin-top: 20px;
      }
    }
  }
}

.aboutbtn {
  margin-top: 50px;
  width: 300px;
  height: 36px;
  line-height: 32px;
  text-align: center;
  font-weight: bold;
  border: 2px solid #ec407a;
  border-radius: 50px;
  cursor: pointer;
  @media (max-width: 768px) {
    margin: 50px auto auto;
  }
  &:hover {
    opacity: 0.8;
  }
}
.aboutwant {
  background-color: #fffbfc;
  margin: auto;
  padding-top: 120px;
  padding-bottom: 120px;
  padding-left: calc((100% - 1140px) / 2);
  padding-right: calc((100% - 1140px) / 2);
  display: flex;
  justify-content: space-between;
  @media (max-width: 768px) {
    width: 100%;
    display: block;
    padding: 0;
  }

  .text {
    width: 400px;
    margin-top: 80px;
    line-height: 50px;
    @media (max-width: 768px) {
      width: calc(100% - 40px);
      margin-left: 20px;
      margin-right: 20px;
      text-align: center;
    }
    > :nth-child(2) {
      margin-top: 30px;
    }
  }
}

.aboutImgCardWrap {
  width: 1140px;
  display: flex;
  flex-wrap: wrap;
  margin: 40px auto;

  @media (max-width: 768px) {
    width: 100%;
  }

  > img {
    width: 540px;
    margin-bottom: 10px;

    &:nth-child(odd) {
      margin-right: 60px;
    }

    @media (max-width: 768px) {
      width: 100%;

      &:nth-child(even) {
        margin-right: 0;
      }
    }
  }
}
